<template>
  <div class="customer-content">
    <el-form inline class="form">
      <el-form-item label="企业名称">
        <el-input
          v-model="initData.name"
          placeholder="请输入名称"
          class="input-name"
          clearable
        ></el-input>
      </el-form-item>
      <el-form-item label="企业代码">
        <el-input
          v-model="initData.code"
          placeholder="请输入名称"
          class="input-name"
          clearable
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="getList">搜索</el-button>
      </el-form-item>
    </el-form>
    <div class="table-container">
      <el-table :data="pageData.list" height="calc(100vh - 282px)">
        <el-table-column
          prop="companyName"
          label="企业名称"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="companyCode"
          label="企业代码"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="amount"
          label="金额 （元）"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="balance"
          label="余额（元）"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="tradeType"
          label="类型"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="tradeSource"
          label="来源"
          align="center"
          width="150px"
        >
          <template #header>
            <div class="question-content">
              <span>来源</span>
              <el-tooltip placement="top">
                <template #content>
                  <div class="content">
                    <h4>说明</h4>
                    <span>类型明细</span>
                    <div
                      v-for="(item, index) in initData.describe"
                      :key="index"
                    >
                      {{ item }}
                    </div>
                  </div>
                </template>
                <el-icon :size="16" color="#b1b3b8" class="question">
                  <QuestionFilled />
                </el-icon>
              </el-tooltip>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="createdTime"
          label="交易时间"
          align="center"
          width="300px"
        ></el-table-column>
        <template #empty>
          <TableEmpty empty-text="暂无明细"></TableEmpty>
        </template>
      </el-table>
      <!--   分页   -->
      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      ></PagePagination>
    </div>
  </div>
</template>
<script lang="ts" setup>
import { shallowReactive } from "vue";
import { LocalStorage, usePage } from "jz-tool-lib";

import { pageEcSubTransactionApi } from "@api/financial-center/bill-management/customer-details";
import { QuestionFilled } from "@element-plus/icons-vue";
const agentId = LocalStorage.getItem("bd_agentId");
const initData = shallowReactive({
  name: "",
  code: "",
  describe: [
    "1、手动入账：EC代理商为客户充值的资金记录；",
    "2、手动出账：EC代理商扣除客户资金的操作记录；",
    "3、套餐费用：代理商为客户添加EC坐席时，需要支付套餐费用 = 套餐分钟数 * 套餐内分钟数价格",
    "4、坐席费用：代理商为客户添加EC坐席时，需要支付的坐席费用，为0时不生成",
    "5、小号月租：代理商为客户添加EC坐席时，需要支付的月租费用，根据坐席数匹配计算，收取多少坐席费用；",
    "6、中间号月租：代理商为客户添加EC坐席时，需要支付的月租费用，根据坐席数匹配计算，收取多少坐席费用；",
    "7、套餐外话费:客户消耗套餐外时长时需要按照套餐外计费标准收取话费，每一个小时生成一次账单（实时扣除每小时生成一次账单）；",
  ],
});
const { getList, pageNumberChange, pageSizeChange, pageData } = usePage({
  listApi: pageEcSubTransactionApi,
  props: {
    pageNumber: "pageNo",
    pageSize: "pageSize",
  },
  searchParams() {
    return {
      agencyId: agentId,
      companyName: initData.name,
      companyCode: initData.code,
    };
  },
});
</script>
<style lang="scss" scoped>
.form {
  display: flex;
  justify-content: flex-end;
}
.question-content {
  display: flex;
}
.question {
  margin: 3px 0 0 3px;
}
.content {
  max-height: 200px;
  width: 200px;
  overflow-y: auto;
}
.input-name{
  width: 200px;
}
</style>
