//客户明细
import http from "@assets/http";
import { serviceName } from "@assets/common";
import { arrayHasValue } from "jz-tool-lib";
//明细列表
const tradeSourceList = {
  1: "手动入账",
  2: "手动出账",
  3: "套餐费用",
  4: "坐席费用",
  5: "小号月租",
  6: "中间号月租",
  7: "套餐外话费",
  8: "套餐退款",
};
export const pageEcSubTransactionApi = {
  getData: (params) => {
    return http.get(
      `${serviceName.agentSystem}/ecSubTransaction/pageEcSubTransaction`,
      { params: params }
    );
  },
  handleData: function (data) {
    return {
      list: arrayHasValue(data?.records)
        ? data.records.map((item) => {
            return {
              companyCode: item.companyCode,
              companyName: item.companyName,
              amount: item.amount,
              balance: item.balance,
              tradeType: item.tradeType === 1 ? "收入" : "支出",
              tradeSource: tradeSourceList[item.tradeSource] || "未知来源",
              createdTime: dayjs(item.createdTime).format(
                "YYYY-MM-DD HH:mm:ss"
              ),
            };
          })
        : [],
      total: parseInt(data.total),
    };
  },
};
